.side-bar {
  padding-right: 0.5rem;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.2), 0 4px 6px -2px rgba(0, 0, 0, 0.4);
  background: -webkit-gradient(linear, left bottom, left top, from(rgba(1,186,239,1)), to(rgba(30,234,169,1)));
  background: linear-gradient(0deg, rgba(1,186,239,1) 0%, rgba(30,234,169,1) 100%);
  width: 250px;
  &__logo {
    margin-bottom: 0.75rem;
  }
  &__wrapper {
    padding: 1.5rem;
    -webkit-box-flex: 0;
            flex: none;
    box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.2), 0 4px 6px -2px rgba(0, 0, 0, 0.4);
    background-color: #fff;
    height: 100%;
  }
  &__label {
    padding-top: 0.75rem;
    padding-bottom: 0.75rem;
    text-transform: uppercase;
    color: #848C8E;
  }
  &__sep {
    border-bottom: 1px solid #848C8E;
    display: block;
    margin-top: 0.75rem;
    margin-bottom: 0.75rem;
  }
  &__item {
    padding-top: 0.25rem;
    padding-bottom: 0.25rem;
    &:hover a {
      border-bottom: 2px solid #01BAEF;
    }
  }
  &__active-link {
    border-bottom: 2px solid #01BAEF;
  }
}